@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Staatliches&display=swap');

* {box-sizing: border-box;}

body {
  margin: 0;
  padding: 0;
}

.header h1 {
  text-align: center;
  text-transform: uppercase;
  font-family: 'Staatliches', cursive;
  letter-spacing: 7px;
  margin: 0;
  color:white;
  background-color: rgb(109, 139, 195);
}

.nav {
  background-color: darkgrey;
}

.ghlink, .fblink {
  margin: 20px;
  text-decoration: none;
  width: 20px;
  height: 20px;
  background-color: rgb(238, 236, 241);
  color: black;
  padding: 0 30px;
  transition: .4s;
}

.ghlink:hover, .fblink:hover {
  background-color: rgb(119, 139, 146);
  color:white;
}